import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
//import { PrivacyPolicy } from '../privacy-policy/privacy-policy';
import { Functions } from '../../../providers/service/functions';
import { CheckoutService } from '../../../providers/service/checkout-service';
//import { ShippingAddressForm } from '../shipping-address-form/shipping-address-form';
//import { ShippingPaymentMethod } from '../shipping-payment-method/shipping-payment-method';

@IonicPage()
@Component({
  templateUrl: 'checkout-register.html',
})
export class CheckoutRegister {

  form: any;
  zone:any;
  info: any;
  status: any;
  buttonText: any;
  errorMessage: any;
  public disableSubmit: boolean = false;
  
 
  constructor(public nav: NavController, public service: CheckoutService, params: NavParams, public functions: Functions, public values: Values) {

    this.status = [];
    this.info = {};
    this.buttonText = this.values.lan.text_button_continue;  

    this.service.getCheckoutRegister()
    .then((results) => this.handleForm(results)); 
  }


  handleForm(results){
    this.form = results;
    this.getZone(results.country_id);
  }

  
  getZone(id) {
    this.form.zone_id = null;

    this.service.getZone(id)
    .then((results) => this.zone = results);
  }


  saveRegister() {
    if(this.validateForm()){
      this.disableSubmit= true;
      this.buttonText= this.form.text_loading; 

      this.service.saveCheckoutRegister(this.form)
      .then((results) => this.handleResults(results));
    }
  }

 
  validateForm(){
    if(this.form.agree == undefined || this.form.agree == ""){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_agree);return false}
    if(this.form.firstname == undefined || this.form.firstname == ""){return false}
    if(this.form.lastname == undefined || this.form.lastname == ""){return false}
    if(this.form.email == undefined || this.form.email == ""){return false}
    if(this.form.telephone== undefined || this.form.telephone == ""){return false}
    if(this.form.password == undefined || this.form.password == ""){return false}
    if(this.form.confirm == undefined || this.form.confirm == ""){return false}
    if(this.form.address_1 == undefined || this.form.address_1 == ""){return false}
    if(this.form.city == undefined || this.form.city == ""){return false}
    if(this.form.postcode == undefined || this.form.postcode == ""){return false}
    if(this.form.country_id == undefined || this.form.country_id == ""){return false}

    if(this.form.zone_id == undefined || this.form.zone_id == null){
      this.form.zone_id = "0";
    }
    return true
  }


  handleResults(results){
    this.disableSubmit = false;
    this.buttonText= this.form.button_continue; 
    
    if(results.error != undefined){
      this.status = results.error;

      if(results.error.warning != undefined){
        this.functions.showAlert(this.values.lan.text_error, results.error.warning);
      }
    }
     
    else if(this.form.shipping_address == true){
      this.nav.push('ShippingPaymentMethod');
    }

    else{
      this.nav.push('ShippingAddressForm');
    }
  }


  encodeUrl(href) {
    return href.replace(/&amp;/g, '&');
  }


  loadPolicy(){
    this.info.agree_link = this.encodeUrl(this.form.agree_link);
    this.info.title = this.form.agree_title;
    this.nav.push('PrivacyPolicy', this.info);
  }

}



